@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap");

body {
	background-color: rgb(36, 36, 36);
}

* {
	margin: 0;
	padding: 0;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-weight: 500;
	font-style: normal;
}

a {
	text-decoration: none;
	color: white;
}

ul,
ol {
	list-style: none;
}

header {
	width: 100%;
	height: 60px;
	background-color: #333;
}

header .center {
	width: 90%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

header .center h1 {
	width: 200px;
}

header .center h1 a {
	width: 100%;
	color: white;
	line-height: 55px;
}

header .center nav {
	width: 400px;
}

header .center nav ul {
	width: 100%;
	display: flex;
	justify-content: space-between;
	line-height: 60px;
}

header .center nav ul li {
	color: white;
}

.home {
	width: 90%;
	height: 800px;
	margin: 0 auto;
	display: flex;
	flex-wrap: row wrap;
	align-items: center;
}

.home .typing {
	width: 100%;
	text-align: center;
	color: white;
	font-size: 20px;
}

.about {
	width: 100%;
	height: 800px;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

.about h1 {
	color: #fff;
}

.about .center {
	width: 90%;
	height: 100%;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
}

.about .center .left {
	width: 400px;
	height: 400px;
}

.about .center .left .img-box {
	width: 350px;
	height: 350px;
	overflow: hidden;
	border-radius: 10px;
}

.about .center .left .img-box img {
	width: 100%;
	object-fit: cover;
}

.about .center .right {
	width: 400px;
	height: 400px;
}
.about .center .right h3 {
	width: 100%;
	height: 40px;
	line-height: 50px;
	font-size: 35px;
	color: white;
}

.about .center .right p {
	width: 100%;
	color: #fff;
	font-size: 20px;
	line-height: 50px;
	color: rgb(136, 194, 233);
}

.skill {
	width: 100%;
	height: 800px;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

.skill h1 {
	color: #fff;
}

.skill .center {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	align-items: center;
}

.skill .center .img-box {
	width: 25%;
	overflow: hidden;
}

.skill .center .img-box img {
	width: 100%;
	padding: 60px;
	box-sizing: border-box;
}

.project {
	width: 100%;
	height: 800px;
}

.project h1 {
	width: 100%;
	text-align: center;
	color: #fff;
}

.project .center {
	padding: 100px 0 0;
	width: 90%;
	margin: 0 auto;
	display: flex;
}

.project .center a {
	width: 20%;
	height: 60px;
	line-height: 55px;
	text-align: center;
	border-radius: 30px;
	background-color: #333;
	font-size: 20px;
	margin: 0 20px;
	overflow: hidden;
	border: 1px solid white;
}
